"use client";

import { useQuery } from "@tanstack/react-query";
import { useState, useEffect } from "react";
import { Button } from "@/components/ui/button";
import { Card, CardContent } from "@/components/ui/card";

function useDebounce<T>(value: T, delay: number): T {
  const [debounced, setDebounced] = useState(value);
  useEffect(() => {
    const handler = setTimeout(() => setDebounced(value), delay);
    return () => clearTimeout(handler);
  }, [value, delay]);
  return debounced;
}

type Emoji = {
  name: string;
  category: string;
  htmlCode: string[];
};

export default function EmojiList() {
  const [search, setSearch] = useState("");
  const debouncedSearch = useDebounce(search, 500);
  const [category, setCategory] = useState("");
  const [page, setPage] = useState(1);

  const { data, isLoading, error } = useQuery({
    queryKey: ["emojis", debouncedSearch, category, page],
    queryFn: async () => {
      const params = new URLSearchParams({
        page: page.toString(),
        page_size: "20",
      });

      if (debouncedSearch) params.append("search", debouncedSearch);
      if (category) params.append("category", category);

      const res = await fetch(`http://127.0.0.1:8000/emoji?${params.toString()}`);
      if (!res.ok) throw new Error("–û—à–∏–±–∫–∞ –∑–∞–≥—Ä—É–∑–∫–∏");
      return res.json();
    },
  });

  if (isLoading) return <p>‚è≥ –ó–∞–≥—Ä—É–∑–∫–∞...</p>;
  if (error) return <p>‚ùå –û—à–∏–±–∫–∞ –∑–∞–≥—Ä—É–∑–∫–∏ —ç–º–æ–¥–∑–∏</p>;

  return (
    <div className="space-y-6">
      {/* üîç –ü–æ–∏—Å–∫ */}
      <input
        type="text"
        placeholder="–ü–æ–∏—Å–∫ —ç–º–æ–¥–∑–∏..."
        className="p-2 border rounded w-full mb-4"
        value={search}
        onChange={(e) => {
          setSearch(e.target.value);
          setPage(1);
        }}
      />

      {/* üóÇ –ö–∞—Ç–µ–≥–æ—Ä–∏–∏ */}
      <div className="flex flex-wrap gap-2 mb-6">
        <Button
          variant={category === "" ? "default" : "outline"}
          onClick={() => {
            setCategory("");
            setPage(1);
          }}
        >
          –í—Å–µ
        </Button>
        {data.categories?.map((c: string) => (
          <Button
            key={c}
            variant={category === c ? "default" : "outline"}
            onClick={() => {
              setCategory(c);
              setPage(1);
            }}
          >
            {c}
          </Button>
        ))}
      </div>

      {/* üòÄ –°–ø–∏—Å–æ–∫ —ç–º–æ–¥–∑–∏ */}
      <div className="grid grid-cols-4 gap-4">
        {data.items.map((emoji: Emoji, i: number) => (
          <Card key={i} className="shadow-md hover:shadow-lg transition">
            <CardContent className="flex flex-col items-center justify-center p-4">
                <span className="text-3xl mb-2">
                    {String.fromCodePoint(parseInt(emoji.htmlCode[0].replace("&#", "").replace(";", ""), 10))}
                </span>

                <p className="text-sm font-medium">{emoji.name}</p>
                <p className="text-xs text-gray-500">{emoji.category}</p>
            </CardContent>
          </Card>
        ))}
      </div>

      {/* üìÑ –ü–∞–≥–∏–Ω–∞—Ü–∏—è */}
      <div className="flex justify-between mt-6">
        <Button
          variant="outline"
          disabled={page <= 1}
          onClick={() => setPage((p) => p - 1)}
        >
          ‚¨Ö –ù–∞–∑–∞–¥
        </Button>
        <span className="self-center">
          –°—Ç—Ä–∞–Ω–∏—Ü–∞ {data.page} –∏–∑ {data.total_pages}
        </span>
        <Button
          variant="outline"
          disabled={page >= data.total_pages}
          onClick={() => setPage((p) => p + 1)}
        >
          –í–ø–µ—Ä—ë–¥ ‚û°
        </Button>
      </div>
    </div>
  );
}
